.page {
    width: 100%;
    height: 100%;
    position: absolute;
  }
  

.container {
    width: 100%;
    will-change: contents;
    height: 90%;
    min-height: 566px;
    position: absolute;
    opacity: 0;
    top: 5%;
    margin: 0 auto;
    z-index: 1;
    transform-style: preserve-3d;
    animation: fadeIn 1s forwards;
    animation-delay: 1s;
}


.container.about-page,
.container.portfolio-page {

    h1 {
      cursor: default;
        font-size: 46px;
        line-height: 50px;  
        font: 'Poppins';      
        color: #17252A;
        font-weight: 400;
        margin-top: 0;
        position: relative;
        margin-bottom: 15px;
        left: -10px;

      &:before {
        // content: '<h1>';
        font: 'Poppins';      
        color: #17252A;
        font-size: 18px;
        position: absolute;
        margin-top: -10px;
        left: -10px;
        opacity: 0.6;
        line-height: 18px;
      }

      &:after {
        // content: '<h1/>';
        font: 'Poppins';      
        color: #17252A;
        font-size: 18px;
        line-height: 18px;
        position: absolute;
        left: -30px;
        bottom: -20px;
        // margin-left: 20px;
        opacity: 0.6;
      }
    }

    p {
      cursor: default;
      font-size: 16px;
      color: #fff;
      font-family: sans-serif;
      font-weight: 400;
      max-width: fit-content;
      animation: bounceIn 1s;
      &:nth-of-type(1) {
        animation-delay: 1.1s;
      }
      &:nth-of-type(2) {
        animation-delay: 1.2s;
      }
      &:nth-of-type(3) {
        animation-delay: 1.3s;
      }
      &:nth-of-type(4) {
        animation-delay: 1.4s;
      }

    }

  .text-zone {
    position: absolute;
    left: 18%;
    top: 50%;
    transform: translateY(-50%);
    width: 40%;
    vertical-align: middle;
    display: table-cell;
    max-height: 90%;
  }

  .text-animate-hover {
    &:hover {
      color: #fff;
    }
  }
}


